body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


nav {
	border-top: 1px solid #B5B1B1;
}

nav ul {
	display:-webkit-flex;
	display:flex;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
	-webkit-justify-content:center;
	        justify-content:center;
	background:white;
}

nav ul li {
	list-style:none;
}

nav ul li a {
	text-decoration:none;
	text-align:center;
	display:block;
	padding:10px 10px;
	-webkit-transition:0.2s;
	transition:0.2s;
	border-bottom:1px solid white;
	color:#787777;
}

nav ul li a:hover {
	background:#ffffff;
	border-bottom:1px solid black;
	color:#000000;
}

nav ul li ul {
	display:block;
	position:absolute;
	-webkit-transition: 0.2s;
	transition: 0.2s;
	opacity:0;
	visibility:hidden;
	margin:0px;
	padding:0px;
	background:#E8E8E8;
}

nav ul li:hover ul {
	opacity:100;
	visibility:visible;
}

nav ul li ul li a {
	padding:10px 30px;
}
